#content-body {
	width: 100%;
	height: 100%;
	position: relative;
  background: #252525;
}

.logo {
	font-size: 40px;
	text-align: center;
	position: absolute;
  width: 100%;
  font-family: Roboto Slab, sans-serif;
  color: #ffffff;

  font-weight: 100;
  letter-spacing: 6px;
}

.search-guide {
  font-size: 18px;
  color: #ffffff;
  opacity: 0.6;
  display: inline-block;
  position: relative;
  top: -10%;
}

.search-guide .sub {
  display: inline-block;
  position: relative;
}

.search-input {
	position: absolute;
	top: 48%;
}

.search-button {
	position: absolute;
  top: 40%;
  left: 104%;
  font-size: 24px;
  cursor: pointer;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;

  height: 30px;
  width: 30px;
  color: #444444;
  border-radius: 100px;
}

.search-button:hover {
  color: #d5d5d3;
}

#search-body {
	height: 200px;
	width: 300px;
	top: 50%;
	position: relative;
	margin: 0 auto;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.input-wrapper {
  position: relative;
  width: 350px;
  margin: 50px auto;
  float: left;
  right: 20%;
}

.input-highlight {
  font-size: 30px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  line-height: 70px;
  border-top: 3px solid white;
  position: absolute;
  left: 0;
  bottom: 0;
  max-width: 100%;
  height: 0;
  color: transparent;
  font-family: Roboto Slab, sans-serif;
  overflow: hidden; }

#content-body input {
  height: 60px;
  width: 100%;
  min-width: 100%;
  padding: 0;
  border-radius: 0;
  line-height: 70px;
  background-color: transparent;
  color: white;
  font-size: 25px;
  border: none;
  outline: none;
  border-bottom: 3px solid #333333;
  font-family: Roboto Slab, sans-serif; }
  input:focus + .input-highlight {
    border-top: 3px solid #fbc91b; }


@-webkit-keyframes fadein {
  to {
    opacity: 1;
    top: 0px;
  }
}


@keyframes fadein {
  to {
    opacity: 1;
    top: 0px;
  }
}





